/* Slideshow container */
.slideshow-container {
  will-change: transform; /* Optimización para el navegador */
  position: relative;
  display: flex;
  justify-content: center;
  left: 20%;
  right: 20%;
  width: 60%; /* Ancho del slideshow */
  height: 75vh; /* Altura del slideshow */
  margin-top: 10px; /* Espaciado desde arriba */
  background-color: rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

/* Ajustar las imágenes dentro del slideshow */
.mySlides {
  width: 100%;
  height: 100%;
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  top: 0;
  opacity: 0;
  transition: opacity 0.5s ease;
}

/* Ajuste de las imágenes */
.mySlides img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover; /* Asegura que la imagen se ajuste sin deformarse */
  margin: 0 auto;
}

.container-destacado {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 10px 0;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-align: center;
  background-color: rgba(5, 5, 5, 0.6274509804);
  max-height: 30%;
}

/* Texto dentro del slide */
.text {
  font-family: "Montserrat", system-ui;
  font-size: 20px;
  font-weight: 400;
}

.text2 {
  font-family: "Montserrat", system-ui;
  font-size: 16px;
  margin-top: 5px;
  font-weight: lighter;
}

/* Puntos de navegación */
.puntos {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 10px;
}

.dot {
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active {
  background-color: #413f3f;
}

/* Botones de navegación */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 40%;
  padding: 16px;
  color: white;
  font-size: 40px;
  transition: background-color 0.3s ease;
  user-select: none;
  font-family: "Montserrat", system-ui;
}

.prev {
  left: 0%;
  border-radius: 3px 0 0 3px;
}

.next {
  right: 0%;
  border-radius: 0 3px 3px 0;
}

/* Hover effects on navigation buttons */
.prev:hover, .next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

/* Animaciones para el deslizamiento */
@keyframes slideIn {
  from {
    transform: translateX(100%); /* Entra desde la derecha */
    opacity: 0; /* Comienza oculta */
  }
  to {
    transform: translateX(0);
    opacity: 1; /* Termina visible */
  }
}
@keyframes slideOut {
  from {
    transform: translateX(0);
    opacity: 1; /* Comienza visible */
  }
  to {
    transform: translateX(-100%); /* Sale hacia la izquierda */
    opacity: 0; /* Termina oculta */
  }
}
/* Estilos responsivos para pantallas pequeñas */
@media only screen and (max-width: 300px) {
  .text {
    font-size: 11px;
  }
}
/*styling star rating*/
.container-estrella {
  border-top: 0.5px solid rgb(27, 27, 26);
  padding: 5px;
  margin-top: 5px;
  width: 100%;
}

.rating {
  border: none;
}

.rating-wrap p {
  font-family: "Montserrat", system-ui;
  font-size: 1rem;
  font-style: thin;
  color: rgb(27, 27, 26);
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 20px;
}

.rating > input {
  display: none;
}

.rating > label:before {
  content: "\f005";
  font-family: FontAwesome;
  font-size: 1.5rem;
  display: inline-block;
  cursor: pointer;
  text-align: center;
  margin: 5px;
}

.center .rating {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
}

.rating input {
  display: none;
}

.rating label {
  color: #282525;
  cursor: pointer;
}

.rating label:hover,
.rating label:hover ~ label {
  color: #2ddc76;
}

.rating input:checked ~ label {
  color: #2ddc76;
}

.star-rating {
  direction: row-reverse;
  display: inline-block;
  font-size: 1.5rem; /* Ajusta el tamaño según sea necesario */
}

.star-rating .fa-star,
.star-rating .fa-star-half-o {
  color: #FFD700; /* Color dorado para las estrellas */
}

.star-rating .fa-star.checked {
  color: #FFD700; /* Color dorado para las estrellas llenas */
}

